<template>
  <a-form-item label="" :labelCol="{ span: 3 }">
    <a-radio-group name="correctLevel" :value="correctLevel" @change="changeCorrectLevel">
      <a-radio value="L" :disabled="textLength > max.L"> 7% (L)</a-radio>
      <a-radio value="M" :disabled="textLength > max.M">15% (M)</a-radio>
      <a-radio value="Q" :disabled="textLength > max.Q">25% (Q)</a-radio>
      <a-radio value="H" :disabled="textLength > max.H">30% (H)</a-radio>
    </a-radio-group>
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }" :wrapperCol="{ span: 18 }">
    <template v-for="(item, index) in elements">
      <a-image
        v-if="item.view"
        :key="item.id"
        :width="52"
        :height="52"
        :class="{ active: item === selectedElement }"
        :src="item.view"
        :preview="false"
        placeholder
        @click="changeElement(item)"
      />
    </template>
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }" :wrapperCol="{ span: 18 }">
    <a-image
      v-for="(item, index) in eyes"
      :key="item.id"
      :width="52"
      :height="52"
      :class="{ active: item === selectedEye }"
      :src="item.view"
      :preview="false"
      placeholder
      @click="changeEye(item)"
    />
  </a-form-item>
</template>

<script>
export default {
  name: 'QrShape',
  props: {
    textLength: Number,
    max: Object,
    correctLevel: String,
    elements: Array,
    selectedElement: Object,
    eyes: Array,
    selectedEye: Object
  },
  emits: ['update:correctLevel', 'update:selectedElement', 'update:selectedEye'],
  setup(props, context) {
    return {
      changeCorrectLevel: (event) => context.emit('update:correctLevel', event.target.value),
      changeElement: (element) => context.emit('update:selectedElement', element),
      changeEye: (eye) => context.emit('update:selectedEye', eye)
    };
  }
};
</script>

<style lang="less" scoped>
</style>
