import { Modal } from 'ant-design-vue';
import { Svg } from '@svgdotjs/svg.js';
import 'canvas-toBlob';

export function countStr(str) {
    if (!str) return 0;
    var len = 0;
    for (var i = 0; i < str.length; i++) {
        var charcode = str.charCodeAt(i);
        if (charcode < 0x80) len += 1;
        else if (charcode < 0x800) len += 2;
        else if (charcode < 0xd800 || charcode >= 0xe000) len += 3;
        else {
            // surrogate pair            // UTF-16 encodes 0x10000-0x10FFFF by
            i++; // subtracting 0x10000 and splitting the
            len += 4; // 20 bits of 0x0-0xFFFFF into two halves
        }
    }
    return len;
}

export function filterObj(obj, keys) {
    let filteredObj = {};
    Object.keys(obj).forEach((key) => {
        keys.includes(key) && (filteredObj[key] = obj[key]);
    });
    return filteredObj;
}

export function getTime(delimiter) {
    var date = new Date(),
        time = [
            date.getFullYear(),
            date.getMonth() + 1,
            date.getDate(),
            9999,
            date.getHours(),
            date.getMinutes(),
            date.getSeconds(),
        ].map((val) => {
            return (val < 10 ? '0' : '') + val;
        });
    delimiter = delimiter || '-';
    return time.join(delimiter).replace(`${delimiter}9999${delimiter}`, '_');
}

export function dragUpload(selector, uploadFun) {
    let el = document.querySelector(selector),
        tip = document.createElement('div'),
        count = 0;
    el.style.position = 'relative';
    tip.className = 'mask';
    tip.innerHTML = '<p></p>';

    el.ondragenter = (e) => {
        e.preventDefault();
        !count && el.appendChild(tip);
        count++;
    };
    el.ondragover = (e) => {
        e.preventDefault();
    };
    el.ondragleave = (e) => {
        e.preventDefault();
        count--;
        !count && el.removeChild(tip);
    };
    el.ondrop = (e) => {
        e.preventDefault();
        count = 0;
        !count && el.removeChild(tip);
        if (!e.dataTransfer.files || !e.dataTransfer.files.length) {
            // Modal.error({ content: '!' });
            return;
        }
        uploadFun && uploadFun(e.dataTransfer.files[0]);
    };
}

export function unDragUpload(selector) {
    let el = document.querySelector(selector);
    el.ondragenter = el.ondragover = el.ondragleave = el.ondrop = null;
}

export function isUrl(url) {
    let pattern = /^https?:\/\/(?!localhost|127.0)[\w-]+\.\w+/;
    return pattern.test(url);
}

export async function isBase64Image(url) {
    let image = null;
    if (url.startsWith('data:')) image = await loadImage(url, true);
    return !!image;
}

export function checkImg(file, size) {
    const list = ['image/jpg', 'image/jpeg', 'image/png', 'image/gif', 'image/webp', 'image/svg+xml'];
    if (!file || !file.type || !list.includes(file.type)) {
        Modal.error({ content: ' JPG/PNG/GIF/WEBP/SVG !' });
        return false;
    }
    size = size || 2;
    if (file.size > size * 1024 * 1024) {
        Modal.error({ content: ` ${size} MB!` });
        return false;
    }
    return true;
}

export function readFileAsUrl(file) {
    return new Promise((resolve, reject) => {
        if (!file) return resolve(null);
        let reader = new FileReader();
        reader.onload = (event) => {
            resolve(event.target.result);
        };
        reader.onerror = () => {
            Modal.error({ content: '!' });
            resolve(null);
        };
        reader.readAsDataURL(file);
    });
}

export function loadImage(src, silent) {
    return new Promise((resolve, reject) => {
        if (!src) return resolve(null);
        let img = new Image();
        img.onload = () => {
            resolve(img);
        };
        img.onerror = () => {
            !silent && Modal.error({ content: '!' });
            resolve(null);
        };
        img.src = src;
    });
}

export function resizeImage(img, maxSize) {
    let originWidth = img.width,
        originHeight = img.height,
        scale = maxSize / Math.max(originWidth, originHeight),
        width = Math.floor(originWidth * scale),
        height = Math.floor(originHeight * scale);
    return { width, height };
}

export async function fileToImage(file) {
    let url = await readFileAsUrl(file),
        image = await loadImage(url);
    return image;
}

let globalCanvas = null;
export function initGlobalCanvas() {
    return globalCanvas || (globalCanvas = document.createElement('canvas'));
}

export function imageDataToCanvas(imageData) {
    let canvas = initGlobalCanvas(),
        context = canvas.getContext('2d');
    canvas.width = imageData.width;
    canvas.height = imageData.height;
    context.clearRect(0, 0, imageData.width, imageData.height);
    context.putImageData(imageData, 0, 0);
    // document.body.appendChild(canvas);
    return canvas;
}

export function imageToCanvas(img, maxSize) {
    let canvas = initGlobalCanvas(),
        context = canvas.getContext('2d'),
        { width, height } = resizeImage(img, maxSize);
    canvas.width = width;
    canvas.height = height;
    context.clearRect(0, 0, width, height);
    context.drawImage(img, 0, 0, width, height);
    return canvas;
}

export function imageToBlob(img, size, mime, quality) {
    return new Promise(async (resolve, reject) => {
        let canvas = imageToCanvas(img, size);
        canvas.toBlob((blob) => resolve(blob), mime, quality);
    });
}

export function imageToImageData(img, toSize) {
    let canvas = imageToCanvas(img, toSize),
        context = canvas.getContext('2d');
    return context.getImageData(0, 0, canvas.width, canvas.height);
}

export async function fileToSvg(file) {
    let image = await fileToImage(file);
    if (!image) return null;

    let { width, height } = resizeImage(image, 300),
        base64 =
            file.type === 'image/svg+xml'
                ? image.src
                : imageToCanvas(image, 300).toDataURL('image/jpeg', 0.8);

    let svg = new Svg().move(0, 0).size(width, height).viewbox(0, 0, width, height);
    svg.image(base64).move(0, 0).size(width, height);
    return svg.svg();
}
