<template>
  <a-form-item label="" :labelCol="{ span: 3 }" :wrapperCol="{ span: 14 }">
    <a-textarea
      :value="fontText"
      @change="changeFontText"
      placeholder=" Enter "
      :autoSize="{ minRows: 2, maxRows: 4 }"
      :maxlength="200"
      showCount
      allowClear
    />
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }" :wrapperCol="{ span: 8 }">
    <a-select :value="fontFamily" @change="changeFontFamily">
      <a-select-option v-for="(item, index) in fonts" :key="item.name" :value="item.name">
        {{ item.alias }}
        
        <span :style="{ 'font-family': item.name, 'font-size': '16px' }">
          {{ item.preview }}
        </span>
        
      </a-select-option>
    </a-select>
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }" :wrapperCol="{ span: 8 }">
    <a-slider
      :min="20"
      :max="60"
      :marks="{ 20: '20px', 35: '35px', 60: '60px' }"
      :value="fontSize"
      @change="changeFontSize"
    />
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }">
    <a-checkbox-group name="fontStyle" :value="fontStyle" @change="changeFontStyle">
      <a-checkbox value="bold"><BoldOutlined /> </a-checkbox>
      <a-checkbox value="italic"><ItalicOutlined /> </a-checkbox>
      <a-checkbox value="underline"><UnderlineOutlined /> </a-checkbox>
    </a-checkbox-group>
  </a-form-item>
  <!-- <a-form-item label="" :labelCol="{ span: 3 }">
    <a-radio-group name="textAlign" :value="textAlign" @change="changeTextAlign">
      <a-radio value="start"><AlignLeftOutlined /> </a-radio>
      <a-radio value="middle"><AlignCenterOutlined /> </a-radio>
      <a-radio value="end"><AlignRightOutlined /> </a-radio>
    </a-radio-group>
  </a-form-item> -->
  <a-form-item label="" :labelCol="{ span: 3 }">
    <ColorPicker size="small" :defaultColor="fontColor" @colorChange="changeFontColor"></ColorPicker>
  </a-form-item>
</template>

<script>
import ColorPicker from './ColorPicker.vue';
import {
  BoldOutlined,
  ItalicOutlined,
  UnderlineOutlined,
  AlignLeftOutlined,
  AlignRightOutlined,
  AlignCenterOutlined
} from '@ant-design/icons-vue';

export default {
  name: 'QrFont',
  components: {
    ColorPicker,
    BoldOutlined,
    ItalicOutlined,
    UnderlineOutlined,
    AlignLeftOutlined,
    AlignRightOutlined,
    AlignCenterOutlined
  },
  props: {
    fonts: Array,
    fontText: String,
    fontFamily: String,
    fontColor: String,
    fontSize: Number,
    fontStyle: Array,
    textAlign: String
  },
  emits: [
    'update:fontFamily',
    'update:fontText',
    'update:fontColor',
    'update:fontSize',
    'update:fontStyle',
    'update:textAlign'
  ],
  setup(props, context) {
    return {
      changeFontText: (event) => context.emit('update:fontText', event.target.value),
      changeFontFamily: (font) => context.emit('update:fontFamily', font),
      changeFontColor: (color) => context.emit('update:fontColor', color),
      changeFontSize: (size) => context.emit('update:fontSize', size),
      changeFontStyle: (style) => context.emit('update:fontStyle', style),
      changeTextAlign: (event) => context.emit('update:textAlign', event.target.value)
    };
  }
};
</script>

<style lang="less" scoped>
</style>
