import {
    reactive,
    shallowReactive,
    ref,
    toRefs,
    computed,
    watch,
    nextTick,
    onMounted,
    onBeforeUnmount,
} from 'vue';
import { notification } from 'ant-design-vue';
import request from '@/utils/request';
import FontDetect from '@/utils/fontdetect';
import { countStr, checkImg, filterObj, fileToSvg } from '@/utils/functions';
import ls from 'localstorage-slim';
ls.config.encrypt = true;
ls.config.secret = 50;

let presetKeys = [
    'margin',
    'element',
    'eye',
    'eyeOuter',
    'eyeInner',
    'eyeRotate',
    'fill',
    'frame',
    'qrPosition',
    'textPosition',
];
let resourceKeys = ['elements', 'eyes', 'logos', 'fonts', 'colors', 'gdColors', 'presets', 'expireTime'];
let userConfigKeys = [
    'selectedPreset',
    'selectedElement',
    'selectedEye',
    'fontFamily',
    'fill',
    'eyeOuter',
    'eyeInner',
    'userLogos',
    'logoStyle',
    'logoShadow',
    'fontSize',
    'fontStyle',
    'fontColor',
    'textAlign',
];

export default function useInitQrEditor(qrTextRef) {
    //
    let collections = shallowReactive({
        //resource
        elements: null,
        eyes: null,
        logos: null,
        fonts: null,
        colors: null,
        gdColors: null,
        presets: null,

        //qrconfig
        selectedPreset: null,
        selectedElement: null,
        selectedEye: null,
        fontFamily: '',
        fill: null,
        eyeOuter: '',
        eyeInner: '',
        userLogos: reactive([]),
        selectedLogo: null,
        logoStyle: 'origin',
        logoShadow: true,
        fontSize: 35,
        fontStyle: ['bold'],
        fontColor: '#000000',
        textAlign: 'middle',

        //data
        max: {
            L: Math.floor((23648 - 20) / 8),
            M: Math.floor((18672 - 20) / 8),
            Q: Math.floor((13328 - 20) / 8),
            H: Math.floor((10208 - 20) / 8),
            Z: -1,
        },
        textLength: 0,
        correctLevel: 'Q',
        fontText: '',
    });

    let collectionRefs = toRefs(collections);

    //
    let qrConfig = {
        qrText: qrTextRef,
        correctLevel: computed(() => {
            let level = 'Z';

            for (let key in collections.max) {
                if (collections.textLength <= collections.max[key]) level = key;
            }
            if (
                collections.textLength > collections.max[collections.correctLevel] &&
                collections.correctLevel !== level
            ) {
                collections.correctLevel = level; //collectionsgetter
            }
            return collections.correctLevel;
        }),
        margin: ref(20),
        frame: ref(''),
        background: ref(''),
        logo: computed(() =>
            collections.selectedLogo ? collections.selectedLogo.path : collections.selectedLogo
        ),
        eye: computed(() =>
            collections.selectedEye ? collections.selectedEye.path : collections.selectedEye
        ),
        element: computed(() =>
            collections.selectedElement ? collections.selectedElement.path : collections.selectedElement
        ),
        fill: collectionRefs.fill,
        eyeOuter: collectionRefs.eyeOuter,
        eyeInner: collectionRefs.eyeInner,
        eyeRotate: ref(true),
        logoStyle: collectionRefs.logoStyle,
        logoShadow: collectionRefs.logoShadow,
        fontText: collectionRefs.fontText,
        fontFamily: collectionRefs.fontFamily,
        fontSize: collectionRefs.fontSize,
        fontColor: collectionRefs.fontColor,
        fontStyle: collectionRefs.fontStyle,
        textAlign: collectionRefs.textAlign,
        qrPosition: ref(null),
        textPosition: ref(null),
    };

    function applyPreset(config, old) {
        dblog('--- QrEditor set preset ---', config);
        presetKeys.forEach((key) => {
            switch (key) {
                case 'margin':
                    qrConfig.margin.value = Number.parseInt(config.margin) || 20;
                    break;
                case 'element':
                    if (Object.keys(config.element).length) {
                        collections.selectedElement = { presetId: config.id, path: config.element };
                    } else if (config.element && typeof config.element === 'string') {
                        let element = collections.elements.filter((value) => value.path === config.element);
                        collections.selectedElement = element[0] || null;
                    } else {
                        let id = Number.parseInt(config.element) || 0,
                            element = collections.elements.filter((value) => value.id === id);
                        collections.selectedElement = element[0] || null;
                    }
                    break;
                case 'eye':
                    if (config.eye && typeof config.eye === 'string') {
                        collections.selectedEye = { presetId: config.id, path: config.eye };
                    } else {
                        let id = Number.parseInt(config.eye) || 0,
                            eye = collections.eyes.filter((value) => value.id === id);
                        collections.selectedEye = eye[0] || null;
                    }
                    break;
                case 'fill':
                    if ((config.fill && typeof config.fill === 'string') || Array.isArray(config.fill)) {
                        collections.fill = config.fill;
                    } else collections.fill = null; //
                    break;
                case 'frame':
                case 'eyeInner':
                case 'eyeOuter':
                    qrConfig[key].value = config[key] && typeof config[key] === 'string' ? config[key] : '';
                    break;
                case 'eyeRotate':
                    qrConfig[key].value = config[key] !== 'no';
                    break;
                case 'qrPosition':
                case 'textPosition':
                default:
                    qrConfig[key].value = config[key] || null;
                    break;
            }
        });
    }
    function resetPreset() {
        let textLength = countStr(qrConfig.qrText.value);
        collections.textLength = textLength;
        if (
            textLength > 200 &&
            (Object.keys(collections.selectedPreset.element).length ||
                collections.selectedPreset.element === 6)
        ) {
            notification.warning({
                message: '',
                description: '',
                duration: 8,
            });
            collections.selectedPreset = collections.presets[0].items[0];
            return true;
        }
        return false;
    }
    async function addImage(key, file) {
        if (!collections[key] || !checkImg(file)) return;
        let svg = await fileToSvg(file);
        if (!svg) return;

        let image = {
            id: Date.now(),
            view: `data:image/svg+xml;utf8,${encodeURIComponent(svg)}`,
            path: svg,
        };
        collections[key].push(image);
        collections.selectedLogo = collections[key][collections[key].length - 1];
    }
    function removeImage(key, file) {
        if (!collections[key]) return;
        let index = collections[key].indexOf(file);
        if (index < 0) return;

        collections[key].splice(index, 1);
        collections.selectedLogo === file && (collections.selectedLogo = null);
    }
    async function initConfig() {
        let api,
            cache = 60000,
            resource = ls.get('__CACHE__'),
            userConfig = ls.get('__USER__');

        dblog('--- QrEditor init ---');
        if (!resource || !resource.expireTime || !(Date.now() - resource.expireTime < 0)) {
            ({ api, cache, resource } = await request.getSettings());
            dblog('--- QrEditor request for settings ---', resource);

            ['elements', 'eyes', 'logos'].forEach((key) => {
                resource[key] = resource[key].map((item) => ({
                    ...item,
                    view: item.view ? api.home + item.view : '',
                }));
            });
            resource.presets.forEach((category) => {
                category.items = category.items.map((item) => ({ ...item, view: api.home + item.view }));
            });
            let fontDetector = new FontDetect();
            resource.fonts = resource.fonts.filter((item) => fontDetector.detect(item.name));
            resource.expireTime = resource.expireTime || Date.now() + Number.parseInt(cache);
        }
        dblog('--- QrEditor resource ---', resource);

        if (!userConfig || typeof userConfig === 'string') {
            userConfig = {
                selectedPreset: resource.presets[0].items[0],
                fontFamily: resource.fonts[0].name,
            };
            dblog('QrEditor get useConfig from request', userConfig);
        }
        if (userConfig.selectedElement) {
            let data = resource.elements.filter((item) => item.id === userConfig.selectedElement.id);
            userConfig.selectedElement = data[0] || userConfig.selectedElement;
        }
        if (userConfig.selectedEye) {
            let data = resource.eyes.filter((item) => item.id === userConfig.selectedEye.id);
            userConfig.selectedEye = data[0] || userConfig.selectedEye;
        }
        if (userConfig.fill) {
            let data = [...resource.colors, ...resource.gdColors].filter(
                (item) => item.toString() === userConfig.fill.toString()
            );
            userConfig.fill = data[0] || userConfig.fill;
        }
        if (userConfig.userLogos) userConfig.userLogos = reactive(userConfig.userLogos);

        dblog('--- QrEditor useConfig ---', userConfig);
        // useconfig
        Object.assign(collections, resource, { selectedPreset: userConfig.selectedPreset }); //
        nextTick(() => {
            delete userConfig.selectedPreset;
            Object.assign(collections, userConfig); //
        });
        dblog('--- QrEditor init finish ---');
    }
    function saveConfig() {
        if (!collections.selectedPreset) ls.remove('__CACHE__');
        else {
            //  useconfig
            ls.set('__CACHE__', filterObj(collections, resourceKeys));
            ls.set('__USER__', filterObj(collections, userConfigKeys));
        }
    }

    watch(collectionRefs.selectedPreset, applyPreset); // selectedPreset
    onMounted(() => {
        (!window.Blob || !window.URL || !window.URL.createObjectURL) && window.unsupport();
        initConfig();
    });
    onBeforeUnmount(() => saveConfig());
    window.onbeforeunload = () => saveConfig();

    return {
        collectionRefs,
        qrConfig,
        addImage,
        removeImage,
        resetPreset,
    };
}
