<template>
  <a-row :gutter="16">
    <a-col :span="16">
      <div class="left">
        <a-collapse
          v-model:activeKey="activeCollapse"
          expand-icon-position="right"
          :bordered="false"
          accordion
        >
          <template #expandIcon="{ isActive }">
            <span>
              <MinusOutlined v-show="isActive" />
              <PlusOutlined v-show="!isActive" />
            </span>
          </template>

          <!--  content-->
          <a-collapse-panel key="content">
            <template #header>
              <QrcodeOutlined class="title-icon" />
              <h3 class="title"><slot name="contentTitle"></slot></h3>
            </template>

            <p class="tips">
              <!--  -->
              <PushpinFilled class="tips-icon" /><slot name="tips"></slot>
              <!--  -->
              <a-popover
                title=""
                placement="bottomLeft"
                v-model:visible="showHelp"
                :get-popup-container="(trigger) => trigger.parentElement"
              >
                <span class="help"> <QuestionCircleOutlined /></span>
                <template #content><slot name="help"></slot></template>
              </a-popover>
            </p>
            <!-- content  -->
            <slot name="content"></slot>
            <QrStatus :qrStatus="qrStatus" :qrText="qrText" :textLength="textLength" :max="max"></QrStatus>
          </a-collapse-panel>

          <!--  settings-->
          <a-collapse-panel key="settings" class="settings">
            <template #header>
              <SettingFilled class="title-icon" />
              <h3 class="title"><slot name="settingsTitle"></slot></h3>
            </template>

            <!-- settings  -->
            <a-tabs v-model:activeKey="activeSettingsTab" centered :tabBarGutter="50" size="small">
              <!--  -->
              <a-tab-pane key="preset">
                <template #tab>
                  <span><IconFont type="icon-magicwand" /></span>
                </template>
                <QrPresets
                  :textLength="textLength"
                  :presets="presets"
                  v-model:selectedPreset="selectedPreset"
                ></QrPresets>
              </a-tab-pane>
              <!--  -->
              <a-tab-pane key="shape">
                <template #tab>
                  <span><IconFont type="icon-frame" /></span>
                </template>
                <QrShape
                  :textLength="textLength"
                  :max="max"
                  v-model:correctLevel="correctLevel"
                  :elements="elements"
                  v-model:selectedElement="selectedElement"
                  :eyes="eyes"
                  v-model:selectedEye="selectedEye"
                ></QrShape>
              </a-tab-pane>
              <!-- logo -->
              <a-tab-pane key="logo">
                <template #tab>
                  <span><IconFont type="icon-image" />logo</span>
                </template>
                <QrLogo
                  :logos="logos"
                  :userLogos="userLogos"
                  @logoUpload="addImage('userLogos', $event)"
                  @logoDelete="removeImage('userLogos', $event)"
                  v-model:selectedLogo="selectedLogo"
                  v-model:logoStyle="logoStyle"
                  v-model:logoShadow="logoShadow"
                ></QrLogo>
              </a-tab-pane>
              <!--  -->
              <a-tab-pane key="font">
                <template #tab>
                  <span><IconFont type="icon-font" /></span>
                </template>
                <QrFont
                  :fonts="fonts"
                  v-model:fontText="fontText"
                  v-model:fontFamily="fontFamily"
                  v-model:fontColor="fontColor"
                  v-model:fontSize="fontSize"
                  v-model:fontStyle="fontStyle"
                  v-model:textAlign="textAlign"
                ></QrFont>
              </a-tab-pane>
              <!--  -->
              <a-tab-pane key="color">
                <template #tab>
                  <span><IconFont type="icon-color" /></span>
                </template>
                <QrColor
                  :colors="colors"
                  :gdColors="gdColors"
                  v-model:fill="fill"
                  v-model:eyeOuter="eyeOuter"
                  v-model:eyeInner="eyeInner"
                ></QrColor>
              </a-tab-pane>
            </a-tabs>
          </a-collapse-panel>
        </a-collapse>
      </div>
    </a-col>

    <!--  -->
    <a-col :span="8" @click.right.prevent>
      <a-spin size="large" :spinning="qrStatus === 'waiting'">
        <div class="right">
          <QrRender :qrConfig="qrConfig" :resetPreset="resetPreset" @changeQrStatus="changeQrStatus">
          </QrRender>
        </div>
      </a-spin>
    </a-col>
  </a-row>
</template>

<script>
import {
  PlusOutlined,
  MinusOutlined,
  SettingFilled,
  QrcodeOutlined,
  PushpinFilled,
  QuestionCircleOutlined,
  createFromIconfontCN
} from '@ant-design/icons-vue';
import QrStatus from './QrStatus.vue';
import QrPresets from './QrPresets.vue';
import QrShape from './QrShape.vue';
import QrLogo from './QrLogo.vue';
import QrFont from './QrFont.vue';
import QrColor from './QrColor.vue';
import QrRender from './QrRender.vue';

import { reactive, toRef, toRefs, onMounted } from 'vue';
import useInitQrEditor from '@/hooks/useInitQrEditor';

const IconFont = createFromIconfontCN({
  scriptUrl: '//at.alicdn.com/t/font_3395714_5mzms7au0vb.js'
});

export default {
  name: 'QrEditor',
  components: {
    PlusOutlined,
    MinusOutlined,
    SettingFilled,
    QrcodeOutlined,
    PushpinFilled,
    QuestionCircleOutlined,
    IconFont,
    QrStatus,
    QrPresets,
    QrShape,
    QrLogo,
    QrFont,
    QrColor,
    QrRender
  },
  props: {
    qrText: String
  },
  setup(props) {
    let view = reactive({
      activeCollapse: 'content',
      activeSettingsTab: 'preset',
      showHelp: true,
      qrStatus: 'init'
    });

    onMounted(() => {
      view.showHelp = false;
    });

    function changeQrStatus({ status }) {
      view.qrStatus = status;
    }

    let { collectionRefs, qrConfig, addImage, removeImage, resetPreset } = useInitQrEditor(
      toRef(props, 'qrText')
    );

    return {
      ...toRefs(view),
      ...collectionRefs,
      qrConfig,
      changeQrStatus,
      addImage,
      removeImage,
      resetPreset
    };
  }
};
</script>

<style lang="less" scoped>
@import '~@/assets/variable.less';

.left,
.right {
  height: 620px;
  border-radius: 6px;
  overflow: hidden;
}
.right {
  padding: 18px;
  background: @my-bg-white-light;
  display: flex;
  flex-direction: column;
}
.left {
  .ant-collapse {
    background: none;
    :deep(.ant-collapse-item) {
      background: @my-bg-white-light;
      border: none;
      border-radius: 6px;
      overflow: hidden;
      &.settings {
        margin-top: 16px;
      }
      .ant-collapse-header {
        // background: @my-bg-white-lighter;
        line-height: 47px;
        padding: 0;
        &:hover {
          // background: #f0f7ff;
          background: @my-bg-white-light;
        }
        .title-icon {
          display: block;
          width: 48px;
          height: 47px;
          line-height: 50px;
          border-right: 1px solid @border-color-base;
          background: @my-bg-white-light;
          font-size: 22px;
          position: absolute;
          top: 0;
          left: 0;
        }
        .title {
          margin: 0 0 0 60px;
          font-size: 15px;
          font-weight: bold;
        }
        .ant-collapse-arrow {
          font-size: 18px;
        }
      }
      &.ant-collapse-item-active {
        .ant-collapse-header {
          background: @my-bg-white-light;
          box-shadow: 0px 0px 4px 0px rgba(100, 100, 100, 0.5);
          .title {
            color: @primary-color;
          }
          .title-icon {
            color: #ffffff;
            background: @primary-color;
            border-right: 1px solid @primary-color;
          }
        }
      }
      .ant-collapse-content {
        .ant-collapse-content-box {
          padding: 10px;
          height: 510px;
          overflow: hidden;
          .tips {
            margin-bottom: 10px;
            padding-left: 8px;
            .tips-icon {
              margin-right: 6px;
            }
          }
          .ant-tabs {
            height: 100%;
            .ant-tabs-nav {
              margin: 0;
            }
            .ant-tabs-content {
              padding-top: 15px;
              height: 100%;
              overflow: auto;
            }
          }
        }
      }
    }
  }
}
</style>
