<template>
  <a-form-item label="" :labelCol="{ span: 3 }">
    <span
      v-for="(item, index) in colors"
      :key="index"
      :class="['color-block', item === fill ? 'active' : '']"
      @click="changeFill(item)"
    >
      <div :style="{ background: item }"></div>
    </span>
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }">
    <ColorPicker showActive :defaultColor="singleColor" @colorChange="changeFill($event)"></ColorPicker>
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }">
    <span
      v-for="(item, index) in gdColors"
      :key="index"
      :class="['color-block', item === fill ? 'active' : '']"
      @click="changeFill(item)"
    >
      <div :style="{ background: `linear-gradient(135deg, ${item[0]}, ${item[1]})` }"></div>
    </span>
  </a-form-item>
  <br />
  <a-form-item label="" :labelCol="{ span: 3 }">
    <ColorPicker
      showActive
      :defaultColor="eyeOuter"
      @colorChange="changeEyeColor('outer', $event)"
    ></ColorPicker>
  </a-form-item>
  <a-form-item label="" :labelCol="{ span: 3 }">
    <ColorPicker
      showActive
      :defaultColor="eyeInner"
      @colorChange="changeEyeColor('inner', $event)"
    ></ColorPicker>
  </a-form-item>
</template>

<script>
import { computed, toRefs } from 'vue';
import ColorPicker from './ColorPicker.vue';

export default {
  name: 'QrColor',
  components: {
    ColorPicker
  },
  props: {
    colors: Array,
    gdColors: Array,
    fill: [Object, String],
    eyeOuter: String,
    eyeInner: String
  },
  emits: ['update:fill', 'update:eyeOuter', 'update:eyeInner'],
  setup(props, context) {
    let { fill } = toRefs(props);

    let singleColor = computed(() => {
      if (
        Array.isArray(fill.value) &&
        fill.value.length === 1 &&
        props.colors.every((item) => !item.includes(fill.value[0]))
      ) {
        return fill.value[0];
      }
      return '';
    });

    function changeFill(fill) {
      if (fill && !Array.isArray(fill)) fill = [fill];
      context.emit('update:fill', fill || null);
    }
    function changeEyeColor(type, color) {
      if (type === 'outer') context.emit('update:eyeOuter', color || '');
      if (type === 'inner') context.emit('update:eyeInner', color || '');
    }
    return {
      singleColor,
      changeFill,
      changeEyeColor
    };
  }
};
</script>

<style lang="less" scoped>
@import '~@/assets/variable.less';

.color-block {
  display: inline-block;
  vertical-align: middle;
  padding: 1px;
  margin: 0 8px 0 0;
  border-radius: 5px;
  transition: all 0.3s;
  div {
    width: 35px;
    height: 35px;
    border-radius: 5px;
  }
  &.active {
    box-shadow: 0 0 4px 2px @primary-color;
  }
}
</style>
