<template>
  <div class="box" ref="qrEle"></div>
  <div class="save">
    <a-divider dashed></a-divider>
    <a-form-item label="" :labelCol="{ span: 5 }" :wrapperCol="{ span: 18 }" labelAlign="left">
      <a-slider
        :min="300"
        :max="1500"
        :marks="{ 300: '300px', 500: '500px', 1000: '1000px', 1500: '1500px' }"
        :step="50"
        v-model:value="size"
      />
    </a-form-item>
    <a-form-item label="" :labelCol="{ span: 5 }" labelAlign="left" style="margin-bottom: 0">
      <a-button type="primary" :[buttonStatus]="true" @click="downloadQrImage('png')">
        <template #icon><DownloadOutlined /></template>
        <span>PNG</span>
      </a-button>
      <a-button type="primary" :[buttonStatus]="true" @click="downloadQrImage('jpeg')">
        <template #icon><DownloadOutlined /></template>
        <span>JPEG</span>
      </a-button>
      <a-button type="primary" :[buttonStatus]="true" @click="downloadQrImage('svg')" danger>
        <template #icon><DownloadOutlined /></template>
        <span>SVG </span>
      </a-button>
    </a-form-item>
  </div>
</template>

<script>
import CreateQr from '@/utils/CreateQr';
import { computed, reactive, toRefs, watch } from 'vue';
import { DownloadOutlined } from '@ant-design/icons-vue';

export default {
  name: 'QrRender',
  components: {
    DownloadOutlined
  },
  props: {
    qrConfig: Object,
    resetPreset: Function
  },
  emits: ['changeQrStatus'],
  setup(props, context) {
    let qr = null,
      timer = null,
      view = reactive({
        qrEle: null,
        size: 500,
        save: false,
        waiting: false
      });

    watch([...Object.values(props.qrConfig)], (value, old) => {
      if (!view.waiting) {
        context.emit('changeQrStatus', { status: 'waiting' });
        view.waiting = true;
      }

      dblog('--- QrRender config change ---', value, old);
      timer && clearTimeout(timer);
      timer = setTimeout(
        () => {
          dblog('--- QrRender config to render ---', props.qrConfig);
          if (props.resetPreset()) return;
          qr = qr || new CreateQr(view.qrEle);
          qr.render(props.qrConfig); //Qr
          view.waiting = false;

          value[1] === 'Z'
            ? context.emit('changeQrStatus', { status: 'failed' })
            : context.emit('changeQrStatus', { status: !value[0] ? 'init' : 'success' });
        },
        value[0] !== old[0] ? 550 : 200
      );
    });

    let buttonStatus = computed(() => {
      if (!props.qrConfig.qrText.value || props.qrConfig.correctLevel.value === 'Z') return 'disabled';
      if (view.save || view.waiting) return 'loading';
      return '';
    });

    async function downloadQrImage(format) {
      if (buttonStatus.value || !qr) return;
      view.save = true;
      await qr.getQrImage(format, view.size);
      setTimeout(() => (view.save = false), 1500);
    }

    return {
      ...toRefs(view),
      buttonStatus,
      downloadQrImage
    };
  }
};
</script>

<style lang="less" scoped>
@import '~@/assets/variable.less';

.box {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  & > :deep(svg),
  & > :deep(img) {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    border-radius: 5px;
  }
}
.save {
  flex: 0 0 160px;
  margin-top: 10px;
  :deep(.ant-btn) {
    font-family: Arial, Helvetica, sans-serif;
    margin-right: 8px;
    padding: 4px 8px;
    .anticon + span {
      margin-left: 4px;
    }
  }
  .ant-divider{
    border-color: @primary-color
  }
}
</style>
