<template>
  <a-result status="success" v-show="qrStatus === 'success'" title="">
    <template #subTitle>
      <a-popover title="" placement="right">
        <span class="help"> {{ textLength }} </span>
        <template #content>
          <p>{{ qrText }}</p>
        </template>
      </a-popover>
    </template>
  </a-result>
  <a-result status="warning" v-show="qrStatus === 'failed'" title="...">
    <template #subTitle>
      <span class="error-text">
         {{ textLength }}  {{ max.L }} 
      </span>
    </template>
  </a-result>
</template>

<script>
export default {
  name: 'QrStatus',
  props: {
    qrStatus: String,
    qrText: String,
    textLength: Number,
    max: Object
  }
};
</script>

<style scoped>
</style>
