<template>
  <div>
    <template v-for="(category, index) in presets" :key="category.id">
      <a-card
        hoverable
        v-for="(item, index) in category.items"
        :key="item.id"
        class="preset-card"
        :class="{ active: item.id === (selectedPreset || {}).id }"
        @click="changePreset(item)"
      >
        <template #cover>
          <img
            class="preview-img"
            :class="{ blur: textLength > 200 && (Object.keys(item.element).length || item.element === 6) }"
            :src="item.view"
          />
        </template>
        <a-card-meta>
          <template #description>
            <a-tag :color="category.color" :title="`${category.title}`">{{ category.title }}</a-tag>
            <span :title="`${item.title}`">{{ item.title }}</span>
          </template>
        </a-card-meta>
      </a-card>
    </template>
  </div>
</template>

<script>
import { Modal } from 'ant-design-vue';

export default {
  name: 'QrPresets',
  props: {
    presets: Array,
    selectedPreset: Object,
    textLength: Number
  },
  emits: ['update:selectedPreset'],
  setup(props, context) {
    return {
      changePreset: (preset) => {
        if (props.textLength > 200 && (Object.keys(preset.element).length || preset.element === 6)) {
          Modal.error({
            title: '!',
            content: ` 200 !`
          });
          return;
        }
        context.emit('update:selectedPreset', { ...preset });
      }
    };
  }
};
</script>

<style lang="less" scoped>
@import '~@/assets/variable.less';

.presets-image {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.preset-card {
  display: inline-block;
  margin: 7px;
  border-radius: 8px;
  border-color: transparent;
  transition: all 0.3s;
  &.active {
    transform: translateY(-3px);
    box-shadow: 0 0 8px 2px @primary-color;
  }
  :deep(.ant-card-cover) {
    width: 150px;
    height: 150px;
    line-height: 136px;
    font-size: 0;
    padding: 8px;
    text-align: center;
    .preview-img {
      display: inline-block;
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 100%;
      border-radius: 5px;
      box-shadow: 0 0 2px 2px #f0f0f0;
      &.blur {
        filter: blur(2px);
      }
    }
  }
  :deep(.ant-card-body) {
    max-width: 148px;
    padding: 10px 5px;
    border-top: 1px dashed #cccccc;
    .ant-card-meta-description {
      white-space: nowrap;
    }
  }
}
:deep(.ant-image-img) {
  border-radius: 10px;
}
</style>
