<template>
  <div class="color-picker" :class="[showActive && currentColor === defaultColor ? 'active' : '', size]">
    <div ref="el"></div>
  </div>
</template>

<script>
import { onMounted, ref, watch, toRefs } from 'vue';
import Pickr from '@simonwep/pickr';

export default {
  name: 'ColorPicker',
  props: {
    size: {
      type: String,
      default: 'large'
    },
    defaultColor: {
      type: String,
      default: ''
    },
    showActive: {
      type: Boolean,
      default: false
    }
  },
  emits: ['colorChange'],
  setup(props, context) {
    let el = ref(null),
      pickr = null,
      currentColor = ref('#000000'),
      { defaultColor } = toRefs(props);

    onMounted(() => {
      pickr = new Pickr({
        el: el.value,
        theme: 'nano',
        container: el.value.parentNode,
        position: 'right-start',
        defaultRepresentation: 'HEX',
        default: defaultColor.value || '#3366ee',
        adjustableNumbers: false,
        components: {
          palette: true,
          preview: true,
          // opacity: true,
          hue: true,
          interaction: {
            input: true,
            // hex: true,
            // rgba: true,
            // clear: true,
            save: true,
            cancel: true
          }
        },
        i18n: {
          'btn:last-color': 'use previous color',
          'btn:save': '',
          'btn:cancel': ''
          // 'btn:clear': ''
        }
      });

      let setColor = (color) => {
        pickr.hide();
        context.emit('colorChange', color ? (currentColor.value = color.toHEXA().toString()) : '');
      };
      pickr
        .on('init', () =>
          defaultColor.value ? (currentColor.value = defaultColor.value) : pickr.setColor(null, true)
        )
        .on('cancel', () => pickr.hide())
        .on('save', setColor);

      watch(defaultColor, (value, old) => {
        if (pickr) {
          pickr.off('save', setColor);

          if (value) currentColor.value = value;
          pickr.setColor(value || null);

          pickr.on('save', setColor);
        }
      });
    });

    return {
      el,
      currentColor
    };
  }
};
</script>

<style lang="less">
@import '~@simonwep/pickr/dist/themes/nano.min.css';
@import '~@/assets/variable.less';

.color-picker {
  display: inline-block;
  line-height: 100%;
  font-size: 16px;
  padding: 1px;
  border-radius: 5px;
  transition: all 0.3s;
}
.color-picker.active {
  box-shadow: 0 0 4px 2px @primary-color;
}
.pickr {
  display: inline-block;
  line-height: 0;
  vertical-align: middle;
}
.pickr .pcr-button {
  width: 35px;
  height: 35px;
  border-radius: 5px;
  vertical-align: middle;
  overflow: hidden;
}
.small .pickr .pcr-button {
  width: 22px;
  height: 22px;
  border-radius: 3px;
}
</style>
