<template>
  <a-form-item label="Logo" :labelCol="{ span: 3 }" :wrapperCol="{ span: 18 }">
    <div
      v-for="(item, index) in logos"
      :key="item.id"
      class="ant-image"
      :class="{ active: item === selectedLogo }"
    >
      <i @click="changeLogo(null)"><CloseCircleOutlined /></i>
      <img class="ant-image-img" :src="item.view" @click="changeLogo(item)" />
    </div>
  </a-form-item>
  <a-form-item label="Logo" :labelCol="{ span: 3 }" :wrapperCol="{ span: 18 }">
    <div
      v-for="(item, index) in userLogos"
      :key="item.id"
      class="ant-image"
      :class="{ active: item === selectedLogo }"
    >
      <i @click="changeLogo(null)"><CloseCircleOutlined /></i>
      <i @click="logoDelete(item)"><MinusCircleOutlined /></i>
      <img class="ant-image-img" :src="item.view" @click="changeLogo(item)" />
    </div>
    <a-upload list-type="picture-card" class="uploader" :show-upload-list="false" :before-upload="logoUpload">
      <div><PlusOutlined /></div>
    </a-upload>
  </a-form-item>
  <a-form-item label="Logo" :labelCol="{ span: 3 }">
    <a-radio-group name="logoStyle" :value="logoStyle" @change="changeLogoStyle">
      <a-radio value="origin"></a-radio>
      <a-radio value="rect"></a-radio>
      <a-radio value="round"></a-radio>
    </a-radio-group>
  </a-form-item>
  <a-form-item label="Logo" :labelCol="{ span: 3 }">
    <a-switch
      :checked="logoShadow"
      checked-children=""
      un-checked-children=""
      @change="changeLogoShadow"
    />
  </a-form-item>
</template>

<script>
import { CloseCircleOutlined, PlusOutlined, MinusCircleOutlined } from '@ant-design/icons-vue';

export default {
  name: 'QrLogo',
  components: {
    CloseCircleOutlined,
    PlusOutlined,
    MinusCircleOutlined
  },
  props: {
    logos: Array,
    userLogos: Array,
    selectedLogo: Object,
    logoStyle: String,
    logoShadow: Boolean
  },
  emits: ['update:selectedLogo', 'update:logoStyle', 'update:logoShadow', 'logoUpload', 'logoDelete'],
  setup(props, context) {
    return {
      changeLogo: (logo) => context.emit('update:selectedLogo', logo),
      changeLogoStyle: (event) => context.emit('update:logoStyle', event.target.value),
      changeLogoShadow: (shadow) => context.emit('update:logoShadow', shadow),
      logoUpload: (file) => {
        context.emit('logoUpload', file);
        return false;
      },
      logoDelete: (file) => context.emit('logoDelete', file)
    };
  }
};
</script>

<style lang="less" scoped>
@import '~@/assets/variable.less';

.ant-image {
  margin-bottom: 8px;
  width: 52px;
  height: 52px;
  i {
    display: none;
    position: absolute;
    top: -5px;
    right: -5px;
    color: #fff;
    background: #faad14;
    border-radius: 50%;
    line-height: 1;
    font-size: 17px;
    cursor: pointer;
  }
  i + i {
    display: block;
    top: auto;
    bottom: -5px;
    background: #ff4d4f;
  }
  &.active i {
    display: block;
  }
}
.uploader {
  width: 52px;
  height: 52px;
  margin-bottom: 8px;
  :deep(.ant-upload) {
    width: 100%;
    height: 100%;
    margin: 0;
    vertical-align: middle;
    background: none;
    border-color: #aaaaaa;
    border-radius: 5px;
    transition: all 0.3s;
    &:hover {
      border-color: @primary-color;
      color: @primary-color;
    }
  }
}
</style>
