import Qrcode from './qrcode.js';
import { saveAs } from 'file-saver';
import { Svg, G, Gradient, Mask, Use, Text, Rect, Path } from '@svgdotjs/svg.js';
import { getTime, imageToBlob, loadImage } from '@/utils/functions';

class CreateQr {
    constructor(ele) {
        this.config = {
            qrText: '',
            correctLevel: 'Q',
            margin: 20,
            background: '#FFFFFF',
            frame: '',
            logo: '',
            eye: '',
            element: null,
            fill: null,
            eyeOuter: '',
            eyeInner: '',
            eyeRotate: true,
            logoStyle: '',
            logoShadow: true,
            fontText: '',
            fontFamily: '',
            fontSize: 35,
            fontColor: '',
            fontStyle: null,
            textAlign: '',
            qrPosition: null,
            textPosition: null,
        };
        this.data = null;
        this.pattern = null;
        this.rowWidth = 10;
        this.qrEle = ele;
        this.maxSize = 0;

        this.root = null;
        this.qr = null;
        this.logo = null;
        this.text = null;
        this.eleName = ['b11', 'b12', 'b21', 'b13', 'b31', 'b22'];
        this.presets = ['rect', 'rect_s', 'circle', 'circle_s', 'rhombus', 'rhombus_s', 'liquid'];

        this.testSvg = null;
        this.paths = {
            defaultElement:
                '<svg width="10" height="10" viewBox="0 0 10 10"><rect x="0" y="0" width="10" height="10" shape-rendering="crispEdges"></rect></svg>',
            defaultEye:
                '<svg width="70" height="70" viewBox="0 0 70 70"><path d="M70,70H0V0h70V70z M10,60h50V10H10V60z M20,20h30v30H20V20z" shape-rendering="crispEdges"/></svg>',
            eyeOuter: '<path d="M70,70H0V0h70V70z M35,10A25,25,0,0,0,10,35A25,25,0,1,0,35,10Z" fill="none"/>',
            eyeInner: '<circle cx="35" cy="35" r="24.5" fill="none"/>',
            white: '<filter id="white_filter" x="-20%" y="-20%" width="140%" height="140%" filterUnits="objectBoundingBox" primitiveUnits="userSpaceOnUse" color-interpolation-filters="linearRGB"><feColorMatrix type="matrix" values="0 0 0 0 255 0 0 0 0 255 0 0 0 0 255 0 0 0 1 0" x="-20%" y="-20%" width="140%" height="140%" in="SourceGraphic" result="colormatrix"/></filter>',
            filter: `<filter id="logo_filter" x="-20%" y="-20%" width="140%" height="140%" filterUnits="objectBoundingBox" primitiveUnits="userSpaceOnUse" color-interpolation-filters="linearRGB"><feDropShadow stdDeviation="2 2" in="SourceGraphic" dx="0" dy="1" flood-color="#000000" flood-opacity="0.5" x="-20%" y="-20%" width="140%" height="140%" result="dropShadow"/></filter>`,
            rectOuter: '<rect rx="6" ry="6" fill="#FFFFFF"></rect>',
            rectInner: '<rect rx="4" ry="4" stroke-width="0.5" stroke="#bbbbbb" fill="none"></rect>',
            roundOuter: '<circle rx="6" ry="6" fill="#FFFFFF"></circle>',
            roundInner: '<circle rx="4" ry="4" stroke-width="0.5" stroke="#bbbbbb" fill="none"></circle>',
        };
    }
    render(config) {
        let step = this.getRenderStep(config);
        this.clearRoot(); //

        dblog('--- QrCode render step ---', step);
        dblog('--- QrCode render start ---', this.config);
        switch (step) {
            case 'empty':
                this.showRunTime(this.renderEmpty);
                break;
            case 'qrText':
                this.showRunTime(this.loadQrData);
            case 'qrBody':
                this.showRunTime(this.renderQrBody);
            case 'qrEye':
                this.showRunTime(this.renderQrEye);
            case 'qrFill':
                this.showRunTime(this.fillQr);
            case 'logo':
                this.showRunTime(this.renderLogo);
            case 'text':
                this.showRunTime(this.renderText);
            default:
                this.showRunTime(this.renderRoot);
        }
        dblog('--- QrCode render finish ---');
    }
    getRenderStep(config) {
        let step = 'qrText',
            temp = {};
        Object.keys(this.config).forEach((key) => {
            if (config[key]) temp[key] = config[key].value;
        });

        if (temp.margin !== this.config.margin || temp.logoShadow !== this.config.logoShadow) {
            step = ''; //margin renderRoot
        }
        if (
            temp.fontText !== this.config.fontText ||
            temp.fontFamily !== this.config.fontFamily ||
            temp.fontSize !== this.config.fontSize ||
            temp.fontColor !== this.config.fontColor ||
            temp.fontStyle !== this.config.fontStyle ||
            temp.textAlign !== this.config.textAlign
        ) {
            step = 'text'; //
        }
        if (temp.logo !== this.config.logo || temp.logoStyle !== this.config.logoStyle) {
            step = 'logo'; //logo
        }
        if (
            temp.fill !== this.config.fill ||
            temp.eyeOuter !== this.config.eyeOuter ||
            temp.eyeInner !== this.config.eyeInner
        ) {
            step = 'qrFill'; //
        }
        if (temp.eye !== this.config.eye) {
            step = 'qrEye'; //
        }
        if (temp.element !== this.config.element) {
            step = 'qrBody'; //
            if (this.data) {
                let len = this.data.length;
                for (let i = 0; i < len; i++)
                    for (let j = 0; j < len; j++) if (this.pattern[i][j] === 9) this.pattern[i][j] = 0;
            }
        }
        if (temp.qrText !== this.config.qrText || temp.correctLevel !== this.config.correctLevel) {
            step = 'qrText'; //
        }
        if (!temp.qrText || temp.correctLevel === 'Z') {
            step = 'empty'; //
        }
        Object.assign(this.config, temp);
        return step;
    }
    renderEmpty() {
        this.initRoot();
        this.root.put(this.paths.rectInner).move(0, 0).size(500, 500).stroke('#000000');
        let text = new Text().rebuild(false).text('').font({
            family: this.config.fontFamily,
            size: 35,
            anchor: 'middle',
            leading: 1.3,
        });
        this.rebuildText(text, 500 / 2, 500 / 2);
        this.root.add(text).size(500, 500).viewbox(0, 0, 500, 500);
    }
    initRoot() {
        return this.root || (this.root = new Svg().addTo(this.qrEle));
    }
    clearRoot() {
        this.root && this.root.clear();
    }
    renderRoot() {
        this.initRoot();
        this.root.rect();

        let defs = this.root.defs();
        let rootW = 500,
            rootH = 500,
            bgColor = this.config.background || '#FFFFFF',
            qrX = this.config.margin,
            qrY = this.config.margin,
            qrW = rootW - 2 * this.config.margin;
        //frame
        if (this.config.frame) {
            let frame = this.root.put(this.config.frame);
            let { width, height } = frame.viewbox(),
                scale = rootW / Math.max(width, height);
            rootW = Math.ceil(width * scale);
            rootH = Math.ceil(height * scale);
            qrX = this.config.qrPosition ? this.config.qrPosition.offsetX : qrX;
            qrY = this.config.qrPosition ? this.config.qrPosition.offsetY : qrY;
            qrW = this.config.qrPosition ? this.config.qrPosition.size : qrW;
            frame.move(0, 0).size(rootW, rootH);
        }
        // qr
        if (this.config.qrText && this.qr) {
            this.qr.main.move(qrX, qrY).size(qrW, qrW);
            this.root.add(this.qr.main);
        }
        // logo
        if (this.config.logo && this.logo) {
            let { width, height } = this.logo.viewbox(),
                scale = this.getLogoSize(qrW) / ((width + height) / 2),
                logoW = Math.ceil(width * scale),
                logoH = Math.ceil(height * scale),
                logoX = qrX + qrW / 2 - logoW / 2,
                logoY = qrY + qrW / 2 - logoH / 2;
            this.logo.move(logoX, logoY).size(logoW, logoH).attr('filter', null);
            if (this.config.logoShadow) {
                defs.put(this.paths.filter);
                this.logo.attr('filter', 'url(#logo_filter)');
            }
            this.root.add(this.logo);
        }
        // text
        if (this.config.fontText && this.text) {
            let { width, height } = this.text.viewbox(),
                textX = rootW / 2,
                textY = rootH + 10 + height / 2;
            rootH = rootH + height + 10 + this.config.margin;
            this.text.center(textX, textY);
            this.root.add(this.text);
        }
        this.root.size(rootW, rootH).viewbox(0, 0, rootW, rootH);
        this.root.get(0).move(0, 0).size(rootW, rootH).fill(bgColor);
    }
    initImageSize() {
        if (!this.maxSize) {
            this.maxSize =
                (Number.parseFloat(window.getComputedStyle(this.qrEle).getPropertyValue('width')) ||
                    this.qrEle.clientWidth) - 1;
        }
        return this.maxSize;
    }
    async getQrImage(format, size) {
        if (!this.config.qrText || this.config.correctLevel === 'Z') return;

        size = size || this.initImageSize();
        if (!['svg', 'png', 'jpeg'].includes(format)) format = 'png';

        let svgStr = this.root.node.outerHTML.replaceAll('&quot;', ''),
            svgBlob = new Blob([svgStr], { type: 'image/svg+xml' }),
            fileName = `tuzim.net_${getTime()}.${format}`;
        switch (format) {
            case 'svg':
                svgBlob && saveAs(svgBlob, fileName);
                break;
            case 'png':
            case 'jpeg':
                let svgSrc = URL.createObjectURL(svgBlob),
                    image = await loadImage(svgSrc),
                    imageBlob = await imageToBlob(image, size, `image/${format}`, 1);
                URL.revokeObjectURL(svgSrc);
                imageBlob && saveAs(imageBlob, fileName);
                break;
        }
    }
    loadQrData() {
        let qrcode = new Qrcode(0, this.config.correctLevel);
        qrcode.addData(this.config.qrText);
        qrcode.make();
        this.data = qrcode.modules; //
        this.pattern = qrcode.pattern; //eyes, adjust
    }
    initQr() {
        if (this.qr) return this.qr.main;

        this.qr = {
            main: null, //svg
            defs: null, //defs
            eye: null, //defs > eye
            element: {}, //defs > element.bxx
            fill: null, //defs > pattern
            qrMask: null, //defs > mask
            eyesMask: null, //defs > mask
            white: null,

            body: null, //main > body
            eyes: null, //main > eyes
        };
        this.qr.main = new Svg().css('overflow', 'visible');
        this.qr.defs = this.qr.main.defs();
        return this.qr.main;
    }
    isArtQr() {
        return Object.keys(this.qr.element).length;
    }
    addQrElements() {
        this.clearQrElements(this.eleName);
        if (!this.presets.includes(this.config.element)) {
            this.eleName.forEach((ele) => {
                if (ele === 'b11' && !this.config.element[ele]) {
                    this.addOrReplace(this.paths.defaultElement, this.qr.defs, this.qr.element, ele);
                }
                if (this.config.element[ele]) {
                    if (Array.isArray(this.config.element[ele])) {
                        this.qr.element[ele] = [];
                        this.config.element[ele].forEach((item, index) => {
                            this.addOrReplace(item, this.qr.defs, this.qr.element[ele], index);
                        });
                    } else if (typeof this.config.element[ele] === 'string') {
                        this.qr.element[ele] = null;
                        this.addOrReplace(this.config.element[ele], this.qr.defs, this.qr.element, ele);
                    }
                }
            });
        }
    }
    clearQrElements(elements) {
        elements.forEach((ele) => {
            if (this.qr.element[ele]) {
                if (Array.isArray(this.qr.element[ele]))
                    this.qr.element[ele].forEach((item) => item.remove());
                else this.qr.element[ele].remove();
                this.qr.element[ele] = null;
            }
        });
    }
    loopQrData(fun) {
        let rows = this.data.length;
        for (let r = 0; r < rows; r++) {
            for (let c = 0; c < rows; c++) {
                if (this.pattern[r][c] >= 3 || !this.data[r][c]) continue;
                fun && fun.call(this, c * this.rowWidth, r * this.rowWidth);
            }
        }
    }
    renderQrBody() {
        this.initQr();
        this.addQrElements();

        let rows = this.data.length,
            size = rows * this.rowWidth,
            qr = new G(),
            pathArray = [];
        switch (this.config.element) {
            case 'rect':
            case 'rect_s':
                let ro = this.config.element === 'rect_s' ? 1 : 0,
                    rw = this.rowWidth - 2 * ro;
                this.loopQrData((x, y) => pathArray.push(`M${x + ro},${y + ro}h${rw}v${rw}h-${rw}v-${rw}Z`));
                qr.put(new Path().attr('shape-rendering', 'crispEdges').plot(pathArray.join('')));
                break;
            case 'circle':
            case 'circle_s':
                let co = this.config.element === 'circle_s' ? 1 : 0,
                    cr = (this.rowWidth - 2 * co) / 2;
                this.loopQrData((x, y) =>
                    pathArray.push(
                        `M${x + cr + co},${y + co}a${cr},${cr},0,0,1,${0},${2 * cr}a${cr},${cr},0,0,1,${0},${
                            -2 * cr
                        }Z`
                    )
                );
                qr.put(new Path().plot(pathArray.join('')));
                break;
            case 'rhombus':
            case 'rhombus_s':
                let hw = this.rowWidth / 2;
                this.config.element === 'rhombus_s'
                    ? this.loopQrData((x, y) =>
                          pathArray.push(
                              `M${
                                  x + hw
                              },${y}a${hw},${hw},0,0,0,${hw},${hw}a${hw},${hw},0,0,0,${-hw},${hw}a${hw},${hw},0,0,0,${-hw},${-hw}a${hw},${hw},0,0,0,${hw},${-hw}Z`
                          )
                      )
                    : this.loopQrData((x, y) =>
                          pathArray.push(`M${x + hw},${y}l${hw},${hw}l${-hw},${hw}l${-hw},${-hw}Z`)
                      );
                qr.put(new Path().plot(pathArray.join('')));
                break;
            case 'liquid':
                for (let r = 0; r < rows; r++) {
                    for (let c = 0; c < rows; c++) {
                        if (this.pattern[r][c] >= 3) continue;
                        this.data[r][c]
                            ? pathArray.push(this.liquidBlackPath(r, c, this.rowWidth, this.rowWidth / 2))
                            : pathArray.push(this.liquidWhitePath(r, c, this.rowWidth, this.rowWidth / 2));
                    }
                }
                qr.put(new Path().plot(pathArray.join('')));
                break;
            default: // art block
                for (let r = 0; r < rows; r++) {
                    for (let c = 0; c < rows; c++) {
                        if (this.pattern[r][c] >= 3 || !this.data[r][c]) continue;
                        qr.use(this.randomEle(this.artElement(r, c))).move(
                            c * this.rowWidth,
                            r * this.rowWidth
                        );
                    }
                }
                break;
        }
        this.addOrReplace(new Mask().add(qr), this.qr.defs, this.qr, 'qrMask');
        this.qr.main.size(size, size).viewbox(0, 0, size, size);
    }
    renderQrEye() {
        this.initQr();
        this.addOrReplace(this.config.eye || this.paths.defaultEye, this.qr.defs, this.qr, 'eye');

        let eyes = new G(),
            size = this.data.length * this.rowWidth;
        eyes.use(this.qr.eye);
        if (this.config.eyeRotate) {
            eyes.use(this.qr.eye).transform({ origin: [size / 2, size / 2], flip: 'x' });
            eyes.use(this.qr.eye).transform({ origin: [size / 2, size / 2], flip: 'y' });
        } else {
            eyes.use(this.qr.eye).transform({ translate: [size - 70, 0] });
            eyes.use(this.qr.eye).transform({ translate: [0, size - 70] });
        }
        this.addOrReplace(new Mask().add(eyes), this.qr.defs, this.qr, 'eyesMask');
    }
    addFillElements() {
        let fill = this.config.fill; // init fill with image
        if (!fill || Array.isArray(fill)) {
            let colors = this.config.fill || ['#000000']; // fill with colors
            fill = new Gradient('linear').from(0, 0).to(1, 1);
            colors.forEach((color, index, arr) =>
                fill.stop({ color, offset: (index ? index + 1 : index) / arr.length })
            );
        }
        this.addOrReplace(fill, this.qr.defs, this.qr, 'fill');
    }
    fillWith(ele, color) {
        this.addOrReplace(this.paths.white, this.qr.defs, this.qr, 'white');
        ele.fill(color).attr('filter', 'url(#white_filter)');
    }
    fillQr() {
        this.initQr();
        let qr = this.qr.qrMask.get(0).attr({ filter: null, fill: null, stroke: null, 'stroke-width': null }), // fill stroke
            eyes = this.qr.eyesMask.get(0).attr({ filter: null, fill: null }), // fill stroke
            qrBody = null,
            eyesBody = null,
            size = this.data.length * this.rowWidth;

        if (this.config.fill || !this.isArtQr()) {
            this.addFillElements();
            this.fillWith(qr, '#FFFFFF');
            this.fillWith(eyes, '#FFFFFF');
            this.config.element === 'liquid' && qr.stroke('#FFFFFF').attr('stroke-width', 1); // liquid

            let body =
                this.qr.fill instanceof Gradient
                    ? new Rect()
                          .move(-5, -5)
                          .size(size + 10, size + 10)
                          .fill(this.qr.fill)
                    : new Use().move(0, 0).size(size, size).use(this.qr.fill);
            qrBody = new G().add(body).maskWith(this.qr.qrMask);
            eyesBody = new G().add(body.clone()).maskWith(this.qr.eyesMask);
        }
        if (this.config.eyeOuter || this.config.eyeInner) {
            if (!eyesBody) {
                this.fillWith(eyes, '#FFFFFF');
                let body = new Rect().move(0, 0).size(size, size).fill('#000000');
                eyesBody = new G().add(body).maskWith(this.qr.eyesMask); //type===2 && !this.config.fill
            }
            let eyeOuter = eyesBody.put(this.paths.eyeOuter).attr('fill', this.config.eyeOuter || 'none');
            eyesBody.put(eyeOuter.clone()).transform({ origin: [size / 2, size / 2], flip: 'x' });
            eyesBody.put(eyeOuter.clone()).transform({ origin: [size / 2, size / 2], flip: 'y' });
            let eyeInner = eyesBody.put(this.paths.eyeInner).attr('fill', this.config.eyeInner || 'none');
            eyesBody.put(eyeInner.clone()).transform({ origin: [size / 2, size / 2], flip: 'x' });
            eyesBody.put(eyeInner.clone()).transform({ origin: [size / 2, size / 2], flip: 'y' });
        }
        qrBody = qrBody || new Use().use(qr);
        eyesBody = eyesBody || new Use().use(eyes);
        this.addOrReplace(qrBody, this.qr.main, this.qr, 'body');
        this.addOrReplace(eyesBody, this.qr.main, this.qr, 'eyes');
    }
    initLogo() {
        return this.logo || (this.logo = new Svg());
    }
    renderLogo() {
        if (!this.config.logo) return;

        switch (this.config.logoStyle) {
            case 'rect':
                this.renderRectLogo();
                break;
            case 'round':
                this.renderRoundLogo();
                break;
            default:
                this.renderOriginLogo();
                break;
        }
    }
    getLogoSize(qrW) {
        let ratio = 4;
        if (this.config.correctLevel === 'M') ratio = 4.5;
        if (this.config.correctLevel === 'L') ratio = 5.25;
        return qrW / ratio;
    }
    normalizeLogoImage(image) {
        let { width, height } = image.viewbox(),
            scale = 50 / Math.min(width, height), //logoimage  50px
            imageW = Math.ceil(width * scale),
            imageH = Math.ceil(height * scale);
        image.size(imageW, imageH);
        return { width: imageW, height: imageH };
    }
    addLogoElements(type) {
        this.initLogo().clear();
        let defs = this.logo.defs(),
            image = defs.put(this.config.logo),
            clip = type !== 'origin' ? defs.clip() : null,
            outer = type !== 'origin' ? this.logo.put(this.paths[`${type}Outer`]) : null,
            body = this.logo.use(image),
            inner = type !== 'origin' ? this.logo.put(this.paths[`${type}Inner`]) : null;

        return { image, clip, outer, body, inner };
    }
    renderRectLogo() {
        let { image, clip, outer, body, inner } = this.addLogoElements('rect'),
            { width, height } = this.normalizeLogoImage(image),
            padding = 4,
            logoW = width + 2 * padding, // 58px
            logoH = height + 2 * padding;

        clip.put(this.paths.rectInner).move(0, 0).size(width, height);
        outer.move(0, 0).size(logoW, logoH);
        body.clipWith(clip).translate(padding, padding);
        inner.move(padding, padding).size(width, height); // 
        this.logo.size(logoW, logoH).viewbox(0, 0, logoW, logoH);
    }
    renderRoundLogo() {
        let { image, clip, outer, body, inner } = this.addLogoElements('round'),
            { width, height } = this.normalizeLogoImage(image),
            padding = 4,
            clipW = Math.min(width, height),
            logoW = clipW + 2 * padding; // 58px

        image.move((clipW - width) / 2, (clipW - height) / 2);
        clip.put(this.paths.roundInner)
            .center(clipW / 2, clipW / 2)
            .size(clipW, clipW);
        outer.center(logoW / 2, logoW / 2).size(logoW, logoW);
        body.clipWith(clip).translate(padding, padding);
        inner.center(logoW / 2, logoW / 2).size(clipW, clipW); // 
        this.logo.size(logoW, logoW).viewbox(0, 0, logoW, logoW);
    }
    renderOriginLogo() {
        let { image } = this.addLogoElements('origin'),
            { width, height } = this.normalizeLogoImage(image);
        this.logo.size(width, height).viewbox(0, 0, width, height);
    }
    initText() {
        return this.text || (this.text = new Svg());
    }
    rebuildText(text, centerX, centerY) {
        this.testSvg = this.testSvg || new Svg().css('visibility', 'hidden'); //display:none  BBox
        this.testSvg.addTo(this.qrEle);
        //  node dom rebuild  rebuild tspan dy  text  BBox
        this.testSvg.put(text).rebuild(true);
        let { width, height } = text.bbox(),
            textW = Math.ceil(width),
            textH = Math.ceil(height);
        text.center(centerX || textW / 2, centerY || textH / 2); //
        this.testSvg.clear().remove();

        return { textW, textH };
    }
    renderText() {
        if (!this.config.fontText) return;

        this.initText().clear();
        //  rebuild node domgetComputedStyle  font-size tspan dy 
        let text = new Text()
            .rebuild(false)
            .fill(this.config.fontColor) //.fill(this.qr.pattern.clone().addTo(this.text.main.defs()))
            .attr('text-decoration', this.config.fontStyle.includes('underline') ? 'underline' : 'none')
            .text(this.config.fontText)
            .font({
                family: this.config.fontFamily,
                size: this.config.fontSize,
                weight: this.config.fontStyle.includes('bold') ? 600 : 400,
                style: this.config.fontStyle.includes('italic') ? 'italic' : 'normal',
                anchor: this.config.textAlign,
                leading: 1.3,
            });
        let { textW, textH } = this.rebuildText(text, 0, 0);
        this.text.add(text).size(textW, textH).viewbox(0, 0, textW, textH);
    }
    addOrReplace(src, target, store, storeKey) {
        store[storeKey] = store[storeKey] ? store[storeKey].replace(src) : target.put(src);
    }
    isBlock(r, c, width, height) {
        let isBlock = 1;
        for (let i = r; i < r + height; i++)
            for (let j = c; j < c + width; j++)
                if (!this.isBlack(i, j) || this.pattern[i][j] >= 3) isBlock = 0;
        return isBlock;
    }
    isBlack(r, c) {
        let len = this.data.length;
        return !(r < 0 || c < 0 || r >= len || c >= len) && this.data[r][c];
    }
    setPatternValue(r, c, width, height, value) {
        let len = this.data.length;
        for (let i = r; i < r + height; i++)
            for (let j = c; j < c + width; j++)
                i < 0 || j < 0 || i >= len || j >= len || (this.pattern[i][j] = value);
    }
    liquidWhitePath(r, c, rowW, cr) {
        let x,
            y,
            str = '';
        if (this.isBlack(r - 1, c) && this.isBlack(r, c - 1)) {
            (x = c * rowW), (y = r * rowW); //top left
            str += `M${x},${y}l${0},${cr}a${cr},${cr},0,0,1,${cr},${-cr}l${-cr},${0}Z`;
        }
        if (this.isBlack(r - 1, c) && this.isBlack(r, c + 1)) {
            (x = c * rowW + rowW), (y = r * rowW); //top right
            str += `M${x},${y}l${-cr},${0}a${cr},${cr},0,0,1,${cr},${cr}l${0},${-cr}Z`;
        }
        if (this.isBlack(r + 1, c) && this.isBlack(r, c + 1)) {
            (x = c * rowW + rowW), (y = r * rowW + rowW); //bottom right
            str += `M${x},${y}l${0},${-cr}a${cr},${cr},0,0,1,${-cr},${cr}l${cr},${0}Z`;
        }
        if (this.isBlack(r + 1, c) && this.isBlack(r, c - 1)) {
            (x = c * rowW), (y = r * rowW + rowW); //bottom left
            str += `M${x},${y}l${cr},${0}a${cr},${cr},0,0,1,${-cr},${-cr}l${0},${cr}Z`;
        }
        return str;
    }
    liquidBlackPath(r, c, rowW, cr) {
        let x = c * rowW,
            y = r * rowW + cr,
            str = `M${x},${y}`;
        this.isBlack(r, c - 1) || this.isBlack(r - 1, c) || this.isBlack(r - 1, c - 1)
            ? (str += `l${0},${-cr}l${cr},${0}`)
            : (str += `a${cr},${cr},0,0,1,${cr},${-cr}`); //top left
        this.isBlack(r - 1, c) || this.isBlack(r, c + 1) || this.isBlack(r - 1, c + 1)
            ? (str += `l${cr},${0}l${0},${cr}`)
            : (str += `a${cr},${cr},0,0,1,${cr},${cr}`); //top right
        this.isBlack(r + 1, c) || this.isBlack(r, c + 1) || this.isBlack(r + 1, c + 1)
            ? (str += `l${0},${cr}l${-cr},${0}`)
            : (str += `a${cr},${cr},0,0,1,${-cr},${cr}`); //bottom right
        this.isBlack(r + 1, c) || this.isBlack(r, c - 1) || this.isBlack(r + 1, c - 1)
            ? (str += `l${-cr},${0}l${0},${-cr}Z`)
            : (str += `a${cr},${cr},0,0,1,${-cr},${-cr}Z`); //bottom left
        return str;
    }
    artElement(r, c) {
        let block = 11,
            density = 100;
        if (this.qr.element.b22 && this.isBlock(r, c, 2, 2)) (block = 22), (density = 40);
        // else if (
        //     this.qr.element.b22 &&
        //     (this.isBlock(r, c + 1, 2, 2) || this.isBlock(r + 1, c, 2, 2) || this.isBlock(r + 1, c - 1, 2, 2))
        // )
        //     block = 11;
        else if (this.qr.element.b13 && this.isBlock(r, c, 1, 3)) (block = 13), (density = 30);
        else if (this.qr.element.b31 && this.isBlock(r, c, 3, 1)) (block = 31), (density = 30);
        else if (this.qr.element.b21 && this.isBlock(r, c, 2, 1)) (block = 21), (density = 20);
        else if (this.qr.element.b12 && this.isBlock(r, c, 1, 2)) (block = 12), (density = 20);
        // else block = 11;

        if (block !== 11) block = this.randomBetween(1, 101) <= density ? block : 11;
        block !== 11 && this.setPatternValue(r, c, Math.floor(block / 10), block % 10, 9);
        return this.qr.element['b' + block];
    }
    randomEle(element) {
        return Array.isArray(element) ? element[this.randomBetween(0, element.length)] : element;
    }
    randomBetween(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min)) + min; //
    }
    showRunTime(fun) {
        let d1 = Date.now();
        fun.call(this);
        dblog(`--- QrCode ${fun.name} ---`, Date.now() - d1, 'ms');
    }
}

export default CreateQr;
